'use strict';
/**
 * @api SET_DRAGBACK(enable) SET_DRAGBACK
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {boolean} enable=false 是否开启滑动返回，默认关闭滑动返回
 * @apiDescription 是否开启滑动返回
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * SET_DRAGBACK()
 * @apiParamExample {string} 返回值
 * 无
 **/
import {WinJSDec} from './APP/config';

var name = 'SET_DRAGBACK';
var SET_DRAGBACK = (enable = false) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('webview', 'dragBackEnable', {enable});
  });
};
export default SET_DRAGBACK;